rection URI')\">\n\t\t\t<input type=\"submit\" class=\"button\" :value=\"t('oidc', 'Add')\">\n\t\t</form>\n\t\t<p style=\"margin-top: 1.5em;\">\n\t\t\t{{ t('oidc', 'Public Key') }}\n\t\t</p>\n\t\t<code>{{ publicKey }}</code>\n\t\t<br>\n\t\t<form @submit.prevent=\"regenerateKeys\">\n\t\t\t<input type=\"submit\" class=\"button\" :value=\"t('oidc', 'Regenerate Keys')\">\n\t\t</form>\n\t</NcSettingsSection>\n</template>\n\n<script>\nimport axios from '@nextcloud/axios'\nimport OIDCItem from './components/OIDCItem.vue'\nimport RedirectItem from './components/RedirectItem.vue'\nimport { generateUrl } from '@nextcloud/router'\nimport NcSettingsSection from '@nextcloud/vue/dist/Components/NcSettingsSection.js'\n\nexport default {\n\tname: 'App',\n\tcomponents: {\n\t\tOIDCItem,\n\t\tRedirectItem,\n\t\tNcSettingsSection,\n\t},\n\tprops: {\n\t\tclients: {\n\t\t\ttype: Array,\n\t\t\trequired: true,\n\t\t},\n\t\texpireTime: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\trefreshExpireTime: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\tpublicKey: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\tgroups: {\n\t\t\ttype: Array,\n\t\t\trequired: true,\n\t\t},\n\t\tlogoutRedirectUris: {\n\t\t\ttype: Array,\n\t\t\trequired: true,\n\t\t},\n\t\tintegrateAvatar: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\toverwriteEmailVerified: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\tdynamicClientRegistration: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tnewClient: {\n\t\t\t\tname: '',\n\t\t\t\tredirectUri: '',\n\t\t\t\tsigningAlg: 'RS256',\n\t\t\t\ttype: 'confidential',\n\t\t\t\terrorMsg: '',\n\t\t\t\terror: false,\n\t\t\t},\n\t\t\tnewLogoutRedirectUri: {\n\t\t\t\tredirectUri: '',\n\t\t\t},\n\t\t\texpTime: this.expireTime,\n\t\t\trefreshExpTime: this.refreshExpireTime,\n\t\t\tintAvatar: this.integrateAvatar,\n\t\t\towEmailVerified: this.overwriteEmailVerified,\n\t\t\tdynClientRegistration: this.dynamicClientRegistration,\n\t\t\terror: false,\n\t\t\terrorMsg: '',\n\t\t\tversion: 0,\n\t\t}\n\t},\n\tmethods: {\n\t\tdeleteRedirectUri(id) {\n\t\t\taxios.delete(generateUrl('apps/oidc/clients/redirect/{id}', { id }))\n\t\t\t\t.then((response) => {\n\t\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\t\tthis.clients.splice(0, this.clients.length)\n\t\t\t\t\tfor (let index = 0; index < response.data.length; index++) {\n\t\t\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\t\t\tthis.clients.push(response.data[index])\n\t\t\t\t\t}\n\t\t\t\t\tthis.version += 1\n\t\t\t\t}).catch(reason => {\n\t\t\t\t\tthis.error = true\n\t\t\t\t\tthis.errorMsg = reason\n\t\t\t\t})\n\t\t},\n\t\taddRedirectUri(id, uri) {\n\t\t\tthis.error = false\n\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/clients/redirect'),\n\t\t\t\t{\n\t\t\t\t\tid,\n\t\t\t\t\tredirectUri: uri,\n\t\t\t\t}\n\t\t\t).then(response => {\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.clients.splice(0, this.clients.length)\n\t\t\t\tfor (let index = 0; index < response.data.length; index++) {\n\t\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\t\tthis.clients.push(response.data[index])\n\t\t\t\t}\n\t\t\t\tthis.version += 1\n\t\t\t}).catch(reason => {\n\t\t\t\tthis.error = true\n\t\t\t\tthis.errorMsg = reason\n\t\t\t})\n\t\t},\n\t\tdeleteLogoutRedirectUri(id) {\n\t\t\taxios.delete(generateUrl('apps/oidc/logoutRedirect/{id}', { id }))\n\t\t\t\t.then((response) => {\n\t\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\t\tthis.logoutRedirectUris = response.data\n\t\t\t\t\tthis.version += 1\n\t\t\t\t}).catch(reason => {\n\t\t\t\t\tthis.error = true\n\t\t\t\t\tthis.errorMsg = reason\n\t\t\t\t})\n\t\t},\n\t\taddLogoutRedirectUri() {\n\t\t\tthis.error = false\n\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/logoutRedirect'),\n\t\t\t\t{\n\t\t\t\t\tredirectUri: this.newLogoutRedirectUri.redirectUri,\n\t\t\t\t}\n\t\t\t).then(response => {\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.logoutRedirectUris = response.data\n\t\t\t\tthis.newLogoutRedirectUri.redirectUri = ''\n\t\t\t\tthis.version += 1\n\t\t\t}).catch(reason => {\n\t\t\t\tthis.error = true\n\t\t\t\tthis.errorMsg = reason\n\t\t\t})\n\t\t},\n\t\tdeleteClient(id) {\n\t\t\taxios.delete(generateUrl('apps/oidc/clients/{id}', { id }))\n\t\t\t\t.then((response) => {\n\t\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\t\tthis.clients = this.clients.filter(client => client.id !== id)\n\t\t\t\t})\n\t\t},\n\t\taddClient() {\n\t\t\tthis.newClient.error = false\n\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/clients'),\n\t\t\t\t{\n\t\t\t\t\tname: this.newClient.name,\n\t\t\t\t\tredirectUri: this.newClient.redirectUri,\n\t\t\t\t\tsigningAlg: this.newClient.signingAlg,\n\t\t\t\t\ttype: this.newClient.type,\n\t\t\t\t}\n\t\t\t).then(response => {\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.clients.push(response.data)\n\n\t\t\t\tthis.newClient.name = ''\n\t\t\t\tthis.newClient.redirectUri = ''\n\t\t\t\tthis.newClient.signingAlg = 'RS256'\n\t\t\t\tthis.newClient.type = 'confidential'\n\t\t\t}).catch(reason => {\n\t\t\t\tthis.newClient.error = true\n\t\t\t\tthis.newClient.errorMsg = reason.response.data.message\n\t\t\t})\n\t\t},\n\t\tsetTokenExpireTime() {\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/expire'),\n\t\t\t\t{\n\t\t\t\t\texpireTime: this.expTime,\n\t\t\t\t}).then((response) => {\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.expTime = response.data.expire_time\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.expireTime = response.data.expire_time\n\t\t\t})\n\t\t},\n\t\tsetRefreshExpireTime() {\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/refreshExpire'),\n\t\t\t\t{\n\t\t\t\t\trefreshExpireTime: this.refreshExpTime,\n\t\t\t\t}).then((response) => {\n\t\t\t\tthis.refreshExpTime = response.data.refresh_expire_time\n\t\t\t})\n\t\t},\n\t\tsetIntegrateAvatar() {\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/integrateAvatar'),\n\t\t\t\t{\n\t\t\t\t\tintegrateAvatar: this.intAvatar,\n\t\t\t\t}).then((response) => {\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.intAvatar = response.data.integrate_avatar\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.integrateAvatar = response.data.integrate_avatar\n\t\t\t})\n\t\t},\n\t\tsetOverwriteEmailVerified() {\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/overwriteEmailVerified'),\n\t\t\t\t{\n\t\t\t\t\toverwriteEmailVerified: this.owEmailVerified,\n\t\t\t\t}).then((response) => {\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.overwriteEmailVerified = response.data.overwrite_email_verified\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.owEmailVerified = response.data.overwrite_email_verified\n\t\t\t})\n\t\t},\n\t\tsetDynamicClientRegistration() {\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/dynamicClientRegistration'),\n\t\t\t\t{\n\t\t\t\t\tdynamicClientRegistration: this.dynClientRegistration,\n\t\t\t\t}).then((response) => {\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.dynamicClientRegistration = response.data.dynamic_client_registration\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.dynClientRegistration = response.data.dynamic_client_registration\n\t\t\t})\n\t\t},\n\t\tregenerateKeys() {\n\t\t\taxios.post(\n\t\t\t\tgenerateUrl('apps/oidc/genKeys'),\n\t\t\t\t{}).then((response) => {\n\t\t\t\t// eslint-disable-next-line vue/no-mutating-props\n\t\t\t\tthis.publicKey = response.data.public_key\n\t\t\t})\n\t\t},\n\t\tupdateGroups(id, groups) {\n\t\t\tthis.error = false\n\n\t\t\taxios.patch(\n\t\t\t\tgenerateUrl('apps/oidc/clients/groups/{id}', { id }),\n\t\t\t\t{\n\t\t\t\t\tid,\n\t\t\t\t\tgroups,\n\t\t\t\t}\n\t\t\t).then(response => {\n\t\t\t\t// Nothing to do\n\t\t\t}).catch(reason => {\n\t\t\t\tthis.error = true\n\t\t\t\tthis.errorMsg = reason\n\t\t\t})\n\t\t},\n\t\tupdateFlowTypes(id, flowTypes) {\n\t\t\tthis.error = false\n\t\t\tlet resultingFlowTypes = 'code'\n\t\t\tif (flowTypes && flowTypes.value === 'code id_token') {\n\t\t\t\tresultingFlowTypes = 'code id_token'\n\t\t\t}\n\n\t\t\taxios.patch(\n\t\t\t\tgenerateUrl('apps/oidc/clients/flows/{id}', { id }),\n\t\t\t\t{\n\t\t\t\t\tid,\n\t\t\t\t\tflowType: resultingFlowTypes,\n\t\t\t\t}\n\t\t\t).then(response => {\n\t\t\t\t// Nothing to do\n\t\t\t}).catch(reason => {\n\t\t\t\tthis.error = true\n\t\t\t\tthis.errorMsg = reason\n\t\t\t})\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n\ttable {\n\t\tmax-width: 800px;\n\t}\n</style>\n"],"sourceRoot":""}]);
